.navLinks span:hover {
    cursor: pointer;
    color: #c8b8db;
}

.contactLinks a {
    margin-right: 10px;
}

.contactLinks i {
    color: #333;
}

.contactLinks i:hover {
    color: #70587c;
}

.lootTypeTile {
    background-size: cover;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    height: 200px;
    position: relative;
    width: 200px;
    transition: 0.3s;
}

.lootTypeTile:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.lootTypeText {
    background-color: #222;
    bottom: 5px;
    color: #fff;
    left: 10px;
    padding: 5px;
    position: absolute;
}

#lootTiles {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

#lootTiles .lootTile {
    margin: 10px;
}

